import React, { useContext, useEffect } from 'react';
import { GLTFClip } from '../../../../../common/types/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { GLTF, GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { MODEL_DEFAULT_DURATION } from '../../../../../common/configs/clip';
import { playerContext } from '../../../..';

type Props = {
  data: GLTFClip;
  onLoaded(data: GLTFClip): void;
};

const GLTFCard: React.FC<Props> = ({ data, onLoaded }) => {
  const { player } = useContext(playerContext);
  useEffect(() => {
    if (!data?.originalWidth) {
      const loader = new GLTFLoader();
      loader.loadAsync(data.url).then((modelData: GLTF) => {
        onLoaded({
          ...data,
          timelineInfo: {
            ...data.timelineInfo,
            duration: MODEL_DEFAULT_DURATION,
          },
          geometryInfo: {
            ...data.geometryInfo,
            size: {
              width: player?.width || 0,
              height: player?.height || 0,
            },
          },
          playRange: {
            start: 0,
            end: MODEL_DEFAULT_DURATION,
          },
          originalWidth: player?.width || 0,
          originalHeight: player?.height || 0,
          modelData,
        });
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded, player?.height, player?.width]);
  return (
    <StyledAssetCard>
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default GLTFCard;
