import * as THREE from 'three';
import { GLTFClip } from '../../common/types/clip';
import ModelHelper from '../painter/modelHelper';
import { ClipBase } from './base';

export class GLTF extends ClipBase<GLTFClip> {
  private modelMaterial?: THREE.MeshBasicMaterial;
  private modelHelper?: ModelHelper;

  public async init() {
    this.modelHelper = new ModelHelper(
      this.config.originalWidth,
      this.config.originalHeight,
      this.config.modelData,
      this.painter,
    );
    this.initRender();
  }

  private async initRender() {
    this.texture = this.modelHelper?.getTexture();
    this.modelMaterial = new THREE.MeshBasicMaterial({
      map: this.texture,
    });
    this.modelMaterial.transparent = true;
    this.sprite = new THREE.Mesh(this.geometry, this.modelMaterial);
    this.sprite.position.z = this.config.zIndex;
    this.updatePosition();
    this.updateRotate();
  }

  public play() {
    this.modelHelper?.play();
  }

  public pause(): void {
    this.modelHelper?.pause();
  }

  public async onUpdate() {
    if (this.modelHelper) {
      this.modelHelper.update(this.player.currentTime - this.config.timelineInfo.start);
      this.texture = this.modelHelper.getTexture();
      this.updateTexture();
    }
  }

  public async destroy() {
    super.destroy();
    this.modelHelper?.destroy();
    this.texture?.dispose();
    this.geometry.dispose();
    this.modelMaterial?.dispose();
  }
}
