import React, { useEffect } from 'react';
import { PagClip } from '../../../../../common/types/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { getPixelRatio, loadPagWasmModule } from '../../../../../common/utils';

type Props = {
  data: PagClip;
  onLoaded(data: PagClip): void;
};

const PagCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data?.originalWidth) {
      fetch(data.url).then((response) => {
        return response.blob();
      }).then(async (blob: Blob) => {
        const file = new File([blob], data.url.replace(/(.*\/)*([^.]+)/i, '$2'));
        const pagWasm = await loadPagWasmModule();
        const pagFile = await pagWasm.PAGFile.load(file);
        const width = pagFile.width() / getPixelRatio();
        const height = pagFile.height() / getPixelRatio();
        const duration = pagFile.duration() / 1_000;
        onLoaded({
          ...data,
          timelineInfo: {
            ...data.timelineInfo,
            duration,
          },
          geometryInfo: {
            ...data.geometryInfo,
            size: {
              width,
              height,
            },
          },
          playRange: {
            start: 0,
            end: duration,
          },
          originalWidth: width,
          originalHeight: height,
          pagFile,
          pag: pagWasm,
        });
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default PagCard;
