import * as THREE from 'three';
import { GLTF } from 'three/examples/jsm/loaders/GLTFLoader';
import Painter from '.';

export default class ModelHelper {
  // 
  private actions: THREE.AnimationAction[] = [];
  private duration: number = 0;
  private mixer?: THREE.AnimationMixer;

  // 
  private modelData: GLTF;
  private width: number;
  private height: number;

  // 
  private frameBuffer: THREE.WebGLRenderTarget;
  private scene = new THREE.Scene();
  private camera: THREE.PerspectiveCamera;
  private painter: Painter;

  // 
  private playing: boolean = false;

  constructor(width: number, height: number, modelData: GLTF, painter: Painter) {
    this.width = width * 2;
    this.height = height * 2;
    this.modelData = modelData;
    this.painter = painter;
    this.frameBuffer = new THREE.WebGLRenderTarget(this.width, this.height, {
      depthBuffer: true,
      stencilBuffer: false,
      minFilter: THREE.LinearFilter,
      magFilter: THREE.LinearFilter,
    });
    this.camera = new THREE.PerspectiveCamera(45, this.width / this.height, 1, 1000);
    this.camera.position.set(0, 1, 5.5);
    // this.camera.lookAt(0, 1, 0);

    this.initScene();
    this.initAnimationMixer();
    this.update(0);
  }

  private initScene() {
    const hemisphereLight = new THREE.HemisphereLight(0xffffff, 0x444444);
    hemisphereLight.position.set(0, 20, 0);
    this.scene.add(hemisphereLight);

    const dirLight = new THREE.DirectionalLight(0xffffff);
    dirLight.position.set(-3, 10, -10);
    dirLight.castShadow = true;
    dirLight.shadow.camera.top = 2;
    dirLight.shadow.camera.bottom = -2;
    dirLight.shadow.camera.left = -2;
    dirLight.shadow.camera.right = 2;
    dirLight.shadow.camera.near = 0.1;
    dirLight.shadow.camera.far = 40;
    this.scene.add(dirLight);

    const model = this.modelData.scene;
    this.scene.add(model);

    model.traverse((object) => {
      if (object.isObject3D) {
        object.castShadow = true;
        object.receiveShadow = true;
      }
    });

    const skeleton = new THREE.SkeletonHelper(model);
    skeleton.visible = false;
    this.scene.add(skeleton);
  }

  public initAnimationMixer() {
    const animations = this.modelData.animations;
    this.mixer = new THREE.AnimationMixer(this.modelData.scene);
    
    // 
    const defaultAction = this.mixer.clipAction(animations[0]);
    // const walkAction = this.mixer.clipAction(animations[3]);
    // const runAction = this.mixer.clipAction(animations[1]);
    this.setActions([defaultAction]);
    this.activate();
  }

  // TODO:
  public setActions(actions: THREE.AnimationAction[]) {
    this.actions = actions;
    this.duration = actions.reduce((previousValue, currentValue) => {
      return previousValue + currentValue.getClip().duration * 1000;
  }, 0);
  }

  public activate() {
    this.actions.forEach((action) => {
      action.play();
    });
  }

  public deactivate() {
    this.actions.forEach((action) => {
      action.stop();
    });
  }

  public play() {
    if (!this.playing) {
      this.actions.forEach((action) => {
        action.paused = false;
      });
    }
  }

  public pause() {
    if (this.playing) {
      this.actions.forEach((action) => {
        action.paused = true;
      });
    }
  }

  public update(currentTime: number) {
    this.mixer?.setTime(currentTime / 1000);
    this.painter.render(this.frameBuffer, this.scene, this.camera);
  }

  public getTexture() {
    return this.frameBuffer.texture;
  }

  public destroy() {
    
  }
}
