import { ClipBase } from './base';
import * as THREE from 'three';
import { PagClip } from '../../common/types/clip';
import { Shader } from '../painter/shader';
import { PAGView } from 'libpag/types/pag-view';
import { getPixelRatio, setStyles } from '../../common/utils';

export class Pag extends ClipBase<PagClip> {
  private canvas: HTMLCanvasElement = document.createElement('canvas');
  private pagView?: PAGView;

  public async init() {
    await this.initPagView();
    this.initRender();
  }

  private async initPagView() {
    setStyles(this.canvas, {
      width: `${this.config.originalWidth * getPixelRatio()}px`,
      height: `${this.config.originalHeight * getPixelRatio()}px`,
    });
    this.pagView = await this.config.pag.PAGView.init(this.config.pagFile, this.canvas);
  }

  private initRender() {
    if (this.pagView && this.canvas) {
      this.texture = new THREE.CanvasTexture(this.canvas);
      this.material = new THREE.ShaderMaterial({
        uniforms: {
          tex: { value: this.texture },
        },
        vertexShader: Shader.GetVertex(),
        fragmentShader: Shader.GetFragment(),
      });
      this.material.uniformsNeedUpdate = true;
      this.material.transparent = true;
      this.sprite = new THREE.Mesh(this.geometry, this.material);
      this.sprite.position.z = this.config.zIndex;
      this.updatePosition();
      this.updateRotate();
    }
  }

  public async onUpdate() {
    if (this.texture && this.pagView) {
      const { player: { currentTime }, config } = this;
      const progress = (currentTime - config.timelineInfo.start) / (config.playRange.end - config.playRange.start);
      await this.pagView.setProgress(progress);
      this.texture.needsUpdate = true;
      this.updateTexture();
    }
  }

  public async destroy() {
    super.destroy();
    this.pagView?.destroy();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}